.item {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  width: 90%;
  height: 17rem;
  border: 1px solid #e8e8e8;
  border-radius: 0.5rem;
  background-color: var(--item-background-color);
  box-shadow: 0 0 2rem 1rem rgba(204, 204, 204, 0.05);
  transition: all 0.3s ease-in-out;
}

.item:hover {
  transform: scale(1.01);
  box-shadow: 0 0 1rem #e9e8e8;
}

.item-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 0.5rem;
  width: 50%;
  height: 100%;
  padding: 0 1rem 0 4rem;
}

.item-links {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}

.item-progress {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 20%;
  height: 100%;
}

.progress-bar {
  width: 6rem;
  height: 6rem;
  border-radius: 50%;
  background:
    radial-gradient(closest-side, white 80%, transparent 80% 100%),
    conic-gradient(var(--medium-blue) 75%, var(--very-light-grey) 0);
}

.completed {
  padding-top: 0.4rem;
}

.item-chapter {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding: 0 2rem 0 4rem;
  width: 30%;
  height: 100%;
  gap: 1.5rem;
}

.item-chapter div {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.btn-update,
.btn-add {
  width: 15rem;
  border-radius: 0.5rem;
  border: none;
  background-color: var(--azure-color);
  transition: all 0.3s ease-in-out;
  height: 3rem;
}

.line-btn {
  width: 0.1rem;
  height: 2.5rem;
  border: solid 1px var(--very-light-grey);
}

.line {
  width: 0.1rem;
  height: 4.5rem;
  margin: 1rem 0 0 3rem;
  border: solid 1px var(--very-light-grey);
}

.btn-comments,
.btn-remove,
.btn-edit {
  background-color: transparent;
  border: none;
}

@media (max-width: 680px),
  screen and (orientation: portrait) {
  .line {
    display: none;
  }

  .item {
    flex-direction: column;
    justify-content: center;
    gap: 1rem;
    height: 30rem;
    padding: 2rem;
  }

  .item-progress,
  .item-info,
  .item-chapter {
    width: 100%;
    padding: 0;
  }

  .item-chapter {
    justify-content: flex-start;
  }

  .item-progress {
    height: 30%;
    gap: 2rem;
    justify-content: center;
    padding: 1rem 0;
  }

  .btn-update {
    width: 100%;
  }

  .item-chapter div {
    width: 100%;
    flex-direction: row;
    gap: 2rem;
    justify-content: center;
    align-items: baseline;
    height: 2rem;
  }
}
